"use client"

import { useState, useEffect, use } from "react"
import { ShoppingCart, Star, Heart, ArrowLeft, Loader2 } from "lucide-react"
import Link from "next/link"
import { useCart } from "@/hooks/use-cart"

interface Product {
    _id: string;
    name: string;
    category: string;
    price: number;
    originalPrice?: number;
    image: string;
    rating?: number;
    reviews?: number;
    artist?: string;
    description?: string;
}

export default function ProductDetailsPage({ params }: { params: Promise<{ id: string }> }) {
    // Unwrap params using React.use() as recommended in Next.js 15
    const unwrappedParams = use(params)
    const { id } = unwrappedParams

    const [product, setProduct] = useState<Product | null>(null)
    const [loading, setLoading] = useState(true)
    const [error, setError] = useState<string | null>(null)
    const [isWishlisted, setIsWishlisted] = useState(false)
    const [isAdded, setIsAdded] = useState(false)
    const { addItem } = useCart()

    useEffect(() => {
        const fetchProduct = async () => {
            try {
                const res = await fetch(`/api/products/${id}`)
                if (!res.ok) {
                    if (res.status === 404) throw new Error("Product not found")
                    throw new Error("Failed to fetch product")
                }
                const data = await res.json()
                setProduct(data.product)
            } catch (err: any) {
                setError(err.message || "Something went wrong")
            } finally {
                setLoading(false)
            }
        }
        fetchProduct()
    }, [id])

    const handleAddToCart = () => {
        if (!product) return
        addItem({
            id: product._id,
            name: product.name,
            price: product.price,
            image: product.image,
            quantity: 1,
        })
        setIsAdded(true)
        setTimeout(() => setIsAdded(false), 2000)
    }

    const handleBuyNow = () => {
        handleAddToCart()
        // Optionally redirect to cart or checkout using: window.location.href = '/cart'
        // For now, consistent with requirements, we add to cart.
    }

    if (loading) {
        return (
            <div className="min-h-screen flex items-center justify-center bg-background">
                <Loader2 className="h-8 w-8 animate-spin text-primary" />
            </div>
        )
    }

    if (error || !product) {
        return (
            <div className="min-h-screen flex flex-col items-center justify-center bg-background gap-4">
                <h1 className="text-2xl font-bold text-destructive">Error</h1>
                <p className="text-muted-foreground">{error || "Product not found"}</p>
                <Link href="/" className="text-primary hover:underline flex items-center gap-2">
                    <ArrowLeft size={16} /> Back to Home
                </Link>
            </div>
        )
    }

    const discount = product.price && product.originalPrice
        ? Math.round(((product.originalPrice - product.price) / product.originalPrice) * 100)
        : 0

    // Mock rating/reviews if not in DB
    const rating = product.rating || 4.5
    const reviews = product.reviews || 0
    const artist = product.artist || "Verified Seller"

    return (
        <div className="min-h-screen bg-background py-8 px-4 sm:px-6 lg:px-8">
            <div className="max-w-7xl mx-auto">
                <Link
                    href="/"
                    className="inline-flex items-center gap-2 text-muted-foreground hover:text-primary mb-8 transition-colors"
                >
                    <ArrowLeft size={20} />
                    Back to Products
                </Link>

                <div className="grid grid-cols-1 lg:grid-cols-2 gap-12">
                    {/* Image Section */}
                    <div className="relative aspect-square lg:aspect-auto lg:h-[600px] rounded-2xl overflow-hidden bg-muted group">
                        <img
                            src={product.image || "/placeholder.svg"}
                            alt={product.name}
                            className="w-full h-full object-cover group-hover:scale-105 transition-transform duration-500"
                        />
                        <button
                            onClick={() => setIsWishlisted(!isWishlisted)}
                            className="absolute top-4 right-4 p-3 bg-white/90 backdrop-blur-sm rounded-full shadow-lg hover:bg-white transition-all transform hover:scale-110"
                        >
                            <Heart
                                size={24}
                                fill={isWishlisted ? "#C85A17" : "none"}
                                color={isWishlisted ? "#C85A17" : "#7A6B5C"}
                            />
                        </button>
                    </div>

                    {/* Details Section */}
                    <div className="flex flex-col space-y-8">
                        <div>
                            <div className="flex items-center gap-2 text-primary font-medium mb-2">
                                <span className="bg-primary/10 px-3 py-1 rounded-full text-sm">
                                    {product.category || "Art"}
                                </span>
                                <span className="text-muted-foreground text-sm">by {artist}</span>
                            </div>
                            <h1 className="text-4xl font-bold text-foreground mb-4 leading-tight">
                                {product.name}
                            </h1>

                            <div className="flex items-center gap-4 mb-6">
                                <div className="flex items-center bg-secondary/30 px-3 py-1 rounded-lg">
                                    {Array.from({ length: 5 }).map((_, i) => (
                                        <Star
                                            key={i}
                                            size={18}
                                            fill={i < Math.floor(rating) ? "#C85A17" : "transparent"}
                                            stroke={i < Math.floor(rating) ? "#C85A17" : "#E0D5C7"}
                                            className="mr-0.5"
                                        />
                                    ))}
                                    <span className="ml-2 text-sm font-medium text-foreground">{rating}</span>
                                </div>
                                <span className="text-muted-foreground text-sm">({reviews} reviews)</span>
                            </div>

                            <div className="flex items-end gap-4 mb-8">
                                <span className="text-4xl font-bold text-primary">
                                    ₹{product.price.toLocaleString("en-IN")}
                                </span>
                                {product.originalPrice && (
                                    <>
                                        <span className="text-xl text-muted-foreground line-through mb-1">
                                            ₹{product.originalPrice.toLocaleString("en-IN")}
                                        </span>
                                        <span className="text-green-600 font-bold mb-1 bg-green-100 px-2 py-0.5 rounded text-sm">
                                            {discount}% OFF
                                        </span>
                                    </>
                                )}
                            </div>
                        </div>

                        <div className="prose prose-stone dark:prose-invert max-w-none">
                            <h3 className="text-lg font-semibold mb-2">Description</h3>
                            <p className="text-muted-foreground leading-relaxed">
                                {product.description || "No description available for this masterpiece."}
                            </p>
                        </div>

                        <div className="flex gap-4 pt-6 mt-auto">
                            <button
                                onClick={handleAddToCart}
                                className={`flex-1 h-14 rounded-xl font-bold text-lg transition-all flex items-center justify-center gap-3 ${isAdded
                                    ? "bg-green-600 text-white shadow-green-200"
                                    : "bg-secondary text-secondary-foreground hover:bg-primary/10 border-2 border-primary/20 hover:border-primary"
                                    }`}
                            >
                                <ShoppingCart size={24} />
                                {isAdded ? "Added to Cart" : "Add to Cart"}
                            </button>

                            <button
                                onClick={handleBuyNow}
                                className="flex-1 h-14 rounded-xl font-bold text-lg bg-primary text-primary-foreground hover:bg-primary/90 shadow-lg shadow-primary/25 hover:shadow-primary/40 transition-all active:scale-[0.98]"
                            >
                                Buy Now
                            </button>
                        </div>

                        <div className="grid grid-cols-3 gap-4 pt-8 border-t border-border">
                            <div className="text-center">
                                <div className="font-semibold text-foreground">Free Shipping</div>
                                <div className="text-xs text-muted-foreground">On orders over ₹999</div>
                            </div>
                            <div className="text-center border-l border-border">
                                <div className="font-semibold text-foreground">Authentic</div>
                                <div className="text-xs text-muted-foreground">100% Original Art</div>
                            </div>
                            <div className="text-center border-l border-border">
                                <div className="font-semibold text-foreground">Secure</div>
                                <div className="text-xs text-muted-foreground">Protected Payments</div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}
